.nav {
  height: 71px;
  width: 100%;
  justify-content: flex-start !important;
  padding: 0 75px;
  color: #fafafa;
  background-color: #3495eb;
  position: absolute;
  top: 0;
  left: 0;
}


.nav h1 {
  font-size: 2.2rem;
}

.switch {
  height: 44px;
  width: 88px;
  background-color: #272727;
  padding: 4px;
  border-radius: 22px;
  position: absolute;
  top: 50%;
  right: 75px;
  transform: translateY(-50%);
}

.switch-selector {
  height: 100%;
  width: 50%;
  justify-content: flex-start !important;
  border-radius: 50%;
  background-color: #3495eb;
}

.switch-left {
  justify-content: flex-start !important;
  background-color: #fafafa;
}

.switch-right {
  justify-content: flex-end !important;
}

@media only screen and (max-width: 1024px) {
  .nav {
    padding: 0 16px;
  }
  .nav h1 {
    font-size: 1.6rem;
  }
  .switch {
    right: 16px;
  }
}
